.SimpleSlider {
  /*height: 100%;*/
  position: relative;
}

.SimpleSliderItem {
  display: grid !important;
  align-items: center;
  justify-items: center;
  height: calc(100vh - 260px);
  width: 100%;
  margin: 0 0 30px 0;
  grid-template-rows: 60px 1fr 100px;
  max-width: 26vw;
}

.TitleSlider {
  margin-bottom: 0 !important;
  font-weight: bold;
  text-align: center;
  color: #464646;
  margin-top: 5vh;
}

.SimpleSliderItem h1 {
  color: #464646;
  margin-top: 3px !important;
}

.slick-slider {
  height: 100%;
}

.SimpleSliderItem p {
  /* margin: 7px 30px; */
  text-align: center;
  max-width: 600px;
  font-weight: bold;
  color: gray;
}
