/* TO-DO this entire styling must be part with styled component */

input[type="range"] {
  background: #0198ff;
}

input[type="range"] {
  -webkit-appearance: none;
  margin: 18px 0;
  width: 100%;
}

input[type="range"]:focus {
  outline: none;
}

input[type="range"]::-webkit-slider-runnable-track {
  width: 100%;
  height: 3px;
  cursor: pointer;
  animate: 0.2s;
  background: #ffffff;
  border-radius: 30%;
  border: 0.2px solid #010101;
}

input[type="range"]::-webkit-slider-thumb {
  border: 1px solid #000000;
  height: 16px;
  border-radius: 50%;
  width: 16px;
  background: #ffffff;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -6.7px;
}

input[type="range"]:focus::-webkit-slider-runnable-track {
  background: #ffffff;
  opacity: 0.5;
}

input[type="range"]::-moz-range-track {
  width: 100%;
  height: 3px;
  cursor: pointer;
  animate: 0.2s;
  background: #3071a9;
  border-radius: 30%;
  border: 0.2px solid #010101;
}

input[type="range"]::-moz-range-thumb {
  border: 1px solid #000000;
  height: 16px;
  border-radius: 50%;
  width: 16px;
  background: #ffffff;
  cursor: pointer;
}

input[type="range"]::-ms-track {
  width: 100%;
  height: 3px;
  cursor: pointer;
  animate: 0.2s;
  background: transparent;
  border-color: transparent;
  border-width: 16px 0;
  color: transparent;
}

input[type="range"]::-ms-fill-lower {
  background: #2a6495;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
}

input[type="range"]::-ms-fill-upper {
  background: #3071a9;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
}

input[type="range"]::-ms-thumb {
  border: 1px solid #000000;
  height: 16px;
  border-radius: 50%;
  width: 16px;
  background: #ffffff;
  cursor: pointer;
}

input[type="range"]:focus::-ms-fill-lower {
  background: #3071a9;
}

input[type="range"]:focus::-ms-fill-upper {
  background: #ffffff;
  opacity: 0.5;
}

body {
  margin: 0;
  padding: 0;
}

.Apps {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

